.popup {
  box-sizing: border-box;
  padding-top: 24px;
  padding-bottom: 75px;
  position: fixed;
  background: #fff;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 5;
  overflow: auto;
  min-height: 100vh; }

@media (min-width: 400px) {
  .popup {
    width: 320px; } }

.popup--right-position {
  position: fixed;
  right: 0;
  left: unset;
  width: 440px;
  box-shadow: -2px 0px 4px rgba(69, 79, 126, 0.02), 16px 0px 52px rgba(14, 24, 80, 0.2);
  padding-top: 0; }
  .popup--right-position .icon {
    position: fixed;
    right: 470px;
    top: 33px; }
