.button {
  position: relative;
  box-sizing: border-box;
  background: #ff3e79;
  border-radius: 16px;
  color: #fff;
  font-weight: 600;
  text-transform: uppercase;
  font-size: 12px;
  line-height: 24px;
  text-align: left;
  border: none;
  width: 119px;
  height: 32px;
  padding-left: 14px;
  transition: ease-in-out 0.2s; }

.button::after {
  position: absolute;
  display: inline-block;
  content: "";
  width: 7px;
  height: 12px;
  background: url(./static/arrow-to-right-white.png) no-repeat;
  top: 10px;
  right: 11px;
  padding-left: 17px; }

.button:hover,
.button:focus,
.button:active {
  background: #41f6d7;
  color: #1b1c21; }

.button:hover::after,
.button:focus::after {
  background: #41f6d7;
  color: #1b1c21;
  background: url(./static/arrow-to-right-black.png) no-repeat; }

.button-show {
  border: none;
  font-size: 16px;
  line-height: 24px;
  background-color: transparent;
  font-weight: 500;
  cursor: pointer; }

.button-show__arrows {
  margin-right: 16px; }

.button-show__arrows--active {
  transform: rotate(180deg); }
