.header {
  display: grid;
  grid-template-columns: 56px auto 120px;
  justify-items: start;
  align-items: center;
  background: #fff;
  height: 88px;
  border-bottom: 1px solid #d9fff5; }

@media (min-width: 540px) {
  .header {
    grid-template-columns: 60px auto 155px 140px; } }

@media (min-width: 1366px) {
  .header {
    display: none; } }

.header__contacts {
  display: none; }

@media (min-width: 540px) {
  .header__contacts {
    display: grid; } }

.header__application-icon {
  position: relative;
  margin-right: 16px; }

@media (min-width: 540px) {
  .header__application-icon {
    margin-left: 38px; } }

.header__application-icon::before {
  content: "";
  display: block;
  position: absolute;
  width: 2px;
  height: 32px;
  background: #eaeaea;
  border-radius: 10%;
  left: -20px;
  top: 5px; }

.header__repair {
  justify-self: end; }
