.form__title {
  margin: 40px 0 24px 0;
  font-size: 24px;
  line-height: 32px;
  position: relative; }

.form__title::before {
  content: "";
  display: block;
  position: absolute;
  background: #41f6d7;
  border-radius: 0px 25px 25px 0;
  width: 4px;
  height: 40px;
  left: -16px;
  top: -6px; }

@media (min-width: 540px) {
  .form__title::before {
    left: -24px; } }

@media (min-width: 1366px) {
  .form__title::before {
    left: -30px; } }

.form__elem {
  border: 1px solid #eaeaea;
  border-radius: 8px;
  width: 100%;
  height: 48px;
  margin-bottom: 16px;
  padding-left: 16px;
  font-weight: 500; }

.form__elem::-webkit-input-placeholder {
  color: #b5b6bc;
  font-size: 16px;
  line-height: 24px; }

.form__elem::-moz-placeholder {
  color: #b5b6bc;
  font-size: 16px;
  line-height: 24px; }

.form__elem:-ms-input-placeholder {
  color: #b5b6bc;
  font-size: 16px;
  line-height: 24px; }

.form__elem::placeholder {
  color: #b5b6bc;
  font-size: 16px;
  line-height: 24px; }

.form__elem--message {
  height: 118px;
  padding-top: 12px; }

.form__text {
  margin: 0 0 24px 0;
  color: #7e7e82; }

.form__link {
  color: #ff3e79;
  transition: ease-in-out 0.3s; }

.form__link:hover {
  color: #41f6d7; }

.form__button {
  width: 136px;
  margin-left: auto;
  margin-right: 0;
  display: block; }
