.info {
  margin-bottom: 24px; }

.info__title {
  margin: 0 0 24px 16px;
  position: relative; }

@media (min-width: 768px) {
  .info__title {
    margin: 0 0 32px 20px; } }

@media (min-width: 1366px) {
  .info__title {
    margin: 0 0 9px 30px; } }

.info__title::before {
  content: "";
  display: block;
  position: absolute;
  background: #41f6d7;
  border-radius: 0px 25px 25px 0;
  width: 4px;
  height: 40px;
  left: -16px; }

@media (min-width: 768px) {
  .info__title::before {
    left: -20px; } }

@media (min-width: 1366px) {
  .info__title::before {
    left: -30px; } }

.info-nav {
  display: flex;
  overflow-x: scroll;
  overflow-y: hidden; }

.info-nav__link {
  color: #7e7e82;
  font-size: 16px;
  line-height: 24px;
  padding: 8px 16px 8px 16px;
  display: block;
  white-space: nowrap;
  border: 2px solid transparent;
  border-radius: 6px; }

.info-nav::-webkit-scrollbar {
  display: none; }

.info-nav__link--active {
  border: 2px solid #b8ffec;
  border-radius: 6px;
  background-color: #ffffff;
  cursor: default; }

.info-nav__link:hover:not(.info-nav__link--active) {
  border: 2px solid #fff;
  border-radius: 6px;
  background-color: #ffffff;
  transition: 0.5s; }

.info-nav__link:focus:not(.info-nav__link--active) {
  border: 2px solid #fff;
  border-radius: 6px;
  background-color: #ffffff;
  outline: #fff; }

.info-nav__item:first-child {
  margin-left: 16px; }

.info-nav__item:last-child {
  margin-right: 16px; }

.info__article {
  margin-top: 24px; }

.article-items__text {
  height: 90px;
  overflow: hidden; }

@media (min-width: 768px) {
  .article-items__text {
    height: 160px; } }

.article-items__text--active {
  height: auto; }

.article-text__par {
  margin: 0;
  padding: 0; }

.article-text__par:last-child {
  padding-top: 16px; }

.article__image {
  width: 100%;
  object-fit: contain; }

.article-items__button-show {
  padding: 0;
  margin-top: 16px;
  margin-bottom: 16px; }

@media (min-width: 500px) {
  .article {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: 1fr; }
  .article__image {
    border-radius: 6px;
    padding-right: 24px; } }

@media (min-width: 1366px) {
  .info__header {
    display: flex;
    justify-content: space-between; }
  .info__repair {
    display: flex;
    text-align: center;
    right: 30px;
    top: 42px; }
  .repair__link {
    font-size: 16px;
    line-height: 24px;
    font-weight: 500;
    color: #1b1c21;
    display: grid;
    grid-template-columns: auto 40px;
    grid-gap: 8px;
    align-items: center; }
  .repair__link:hover,
  .repair__link:focus {
    color: #ff3e79; }
  .repair__link:first-child {
    margin-right: 32px; }
  .info__application-icon,
  .info__status-icon {
    padding-top: 10px;
    padding-left: 1px; }
  .article__image {
    padding-right: 30px; } }
