.brands {
  margin-bottom: 24px; }

@media (min-width: 768px) {
  .slider-brands__wrapper {
    display: grid;
    grid-gap: 24px;
    grid-template-columns: repeat(auto-fill, minmax(220px, 1fr));
    overflow: hidden;
    height: 168px; } }

.slider-brands__wrapper--active {
  height: auto; }

.slider-brands__slide-container {
  width: 240px;
  height: 72px; }

@media (min-width: 768px) {
  .slider-brands__slide-container {
    width: 220px; } }

.slide-container__link {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding-left: 16px;
  width: 240px;
  height: 72px;
  background: #fff;
  border-radius: 6px;
  border: 1px solid #eaeaea;
  box-sizing: border-box;
  margin-right: 16px;
  position: relative; }

@media (min-width: 768px) {
  .slide-container__link {
    width: 220px; } }

.slide-container__link:hover,
.slide-container__link:focus {
  box-shadow: inset 0px 0px 5px #cccccc; }

.slide-container__link::after {
  position: absolute;
  display: block;
  content: "";
  width: 40px;
  height: 40px;
  border: 2px solid #ff3e79;
  box-sizing: border-box;
  border-radius: 20px;
  right: 16px;
  background: url(./static/arrow-to-right.png) no-repeat 52% 52%; }

.slide-container__link:hover::after,
.slide-container__link:focus::after {
  box-shadow: 0 0 7px 1px #ff3e788e; }

@media (min-width: 768px) {
  .brands__button-show {
    display: block; } }
