.aside-menu {
  position: fixed;
  overflow-y: scroll;
  background: #fff;
  width: 100%;
  height: 100%;
  z-index: 4;
  overflow: auto;
  box-sizing: border-box; }

@media (min-width: 400px) {
  .aside-menu {
    width: 320px;
    box-shadow: -2px 0px 4px rgba(69, 79, 126, 0.02), 16px 0px 52px rgba(14, 24, 80, 0.2); } }

@media (min-width: 1366px) {
  .aside-menu {
    position: static;
    box-shadow: none;
    width: 306px;
    display: grid;
    grid-template-rows: 90px 450px 1fr; } }

.menu__header {
  padding-top: 19px;
  display: grid;
  grid-template-columns: 57px auto 40px;
  align-items: center; }

@media (min-width: 1366px) {
  .menu__header {
    grid-template-columns: auto 40px; } }

@media (min-width: 1366px) {
  .menu-header__close-icon {
    display: none; } }

.menu__nav {
  margin-top: 24px; }

.nav {
  margin: 0;
  padding: 0; }

.nav__item {
  margin-bottom: 24px; }

.nav__link {
  font-size: 16px;
  line-height: 24px;
  font-weight: 500;
  position: relative;
  color: #1b1c21; }

.nav__link:hover {
  color: #7e7e82; }

.nav__link::after {
  content: "";
  position: absolute;
  left: 0;
  width: 0;
  height: 3px;
  background: #41f6d7;
  transition: width 0.3s ease-out;
  top: 20px; }

.nav__link:hover:not(.nav__link--active)::after {
  width: 100%; }

.nav__link--active {
  position: relative;
  color: #7e7e82; }

.nav__link--active::before {
  content: "";
  display: block;
  position: absolute;
  background: #41f6d7;
  border-radius: 0px 25px 25px 0;
  width: 4px;
  height: 32px;
  left: -16px;
  top: -6px; }

@media (min-width: 540px) {
  .nav__link--active::before {
    left: -24px; } }

@media (min-width: 1366px) {
  .nav__link--active::before {
    left: -30px; } }

.menu-footer {
  display: flex;
  flex-direction: column;
  margin-bottom: 32px; }

@media (min-width: 1366px) {
  .menu-footer {
    align-self: end; } }

.menu-footer__mail {
  font-size: 16px;
  line-height: 24px;
  margin-top: 16px;
  margin-bottom: 12px;
  font-weight: 600;
  color: #1b1c21; }

.menu-footer__mail:hover,
.menu-footer__tel:hover {
  transform: translateX(40px) scale(1.2);
  transition: 0.3s;
  text-shadow: 0px 0px 10px #41f6d7; }

.menu-footer__tel {
  font-size: 24px;
  line-height: 32px;
  font-weight: 700;
  margin-bottom: 8px;
  color: #1b1c21; }

.langs__button {
  font-family: "TT Lakes", sans-serif;
  font-size: 16px;
  line-height: 24px;
  font-weight: 600;
  margin-right: 7px;
  background: transparent;
  border: none;
  padding: 0;
  cursor: pointer; }

.langs__button {
  font-family: "TT Lakes", sans-serif;
  font-size: 16px;
  line-height: 24px;
  font-weight: 600;
  margin-right: 7px;
  background: transparent;
  border: none;
  padding: 0;
  cursor: pointer; }

.langs__button--active {
  color: #7e7e82; }

.langs__button:hover:not(.langs__button--active) {
  background: #ff3e79;
  border-radius: 50%;
  box-shadow: 0px -1px 0 3px #ff3e79;
  color: #fff; }
