.footer {
  border-top: 1px solid #d9fff5; }

@media (min-width: 768px) {
  .footer {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    align-items: center; } }

.footer__text {
  font-size: 14px;
  line-height: 16px;
  color: #7e7e82;
  text-align: center;
  display: block;
  margin-bottom: 16px; }

.footer__text:first-child {
  margin-top: 16px; }

@media (min-width: 768px) {
  .footer__text:first-child {
    text-align: start; } }

.footer__text:last-child {
  margin-bottom: 19px; }

@media (min-width: 768px) {
  .footer__text:last-child {
    text-align: end; } }

.footer__link:hover,
.footer__link:active {
  color: #1b1c21; }
