@media (min-width: 768px) {
  .slider-repairs__devices {
    height: 160px;
    overflow: hidden;
    display: grid;
    grid-gap: 24px;
    grid-template-columns: repeat(auto-fill, minmax(224px, 1fr)); } }

.slider-repairs__devices--active {
  height: auto; }

.devices__item {
  margin-right: 16px;
  width: 240px;
  height: 160px;
  position: relative; }

.devices-item__link {
  background: #ffffff;
  border: 1px solid #eaeaea;
  border-radius: 6px;
  width: 240px;
  height: 160px;
  display: block;
  box-sizing: border-box; }

.devices-item__link:hover,
.devices-item__link:focus {
  box-shadow: inset 0px 0px 5px #cccccc; }

.devices-link {
  position: relative; }

.devices-link::after {
  position: absolute;
  display: block;
  content: "";
  width: 40px;
  height: 40px;
  border: 2px solid #ff3e79;
  box-sizing: border-box;
  border-radius: 20px;
  right: 16px;
  bottom: 16px;
  background: url(./static/arrow-to-right.png) no-repeat 52% 52%; }

.devices-link:hover::after,
.devices-link:focus::after {
  box-shadow: 0 0 7px 1px #ff3e788e; }

.devices-link__title {
  font-size: 16px;
  font-weight: 500;
  color: #1b1c21;
  margin-top: 16px;
  margin-left: 16px; }

.repairs__button-show {
  margin-bottom: 32px; }

@media (min-width: 768px) {
  .repairs__button-show {
    display: block; } }
