.prices {
  padding-bottom: 40px; }

.services-slide {
  width: 260px;
  height: 200px;
  background-color: #fff;
  border-radius: 6px;
  position: relative;
  padding: 16px 20px 16px 16px;
  box-sizing: border-box; }

.services-slide:hover,
.services-slide:focus {
  box-shadow: inset 0px 0px 5px #cccccc; }

.services-slide__title {
  font-size: 14px;
  line-height: 16px;
  color: #7e7e82;
  margin: 0 0 4px 0; }

.services-slide__text {
  margin: 0 0 16px 0; }

.services-slide__button {
  position: absolute;
  bottom: 16px;
  right: 16px; }

.prices__get-offer {
  font-size: 16px;
  line-height: 24px;
  font-weight: 500;
  color: #1b1c21;
  display: block;
  text-align: left; }

.get-offer__elem {
  position: relative; }

.get-offer__elem::after {
  position: absolute;
  display: inline-block;
  content: "";
  width: 8px;
  height: 12px;
  background: url(./static/arrow-to-right.png) no-repeat;
  top: 5px;
  right: -15px; }

.prices__text {
  line-height: 20px; }

@media (min-width: 500px) {
  .prices__text {
    width: 421px; } }

@media (min-width: 768px) {
  .services {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: repeat(5, 1fr);
    border-radius: 6px; }
  .services-slide {
    width: 100%;
    height: 65px;
    display: grid;
    grid-template-columns: 3fr 1fr 1fr 1fr;
    grid-template-rows: 1fr;
    align-items: center;
    border-radius: 0px;
    justify-items: start;
    position: relative; }
  .services-slide::after {
    position: absolute;
    display: block;
    content: "";
    width: 97%;
    height: 1px;
    background: #eaeaea;
    margin: 0 auto;
    bottom: 0px; }
  .services-slide:first-child {
    border-radius: 6px 6px 0 0; }
  .services-slide:last-child {
    border-radius: 0 0 6px 6px; }
  .services-slide__title {
    display: none; }
  .services-slide__text {
    margin: 0 10px 0 0; }
  .slider-prices {
    padding-top: 40px; }
  .services-slide__title--heading {
    display: block;
    position: absolute;
    top: -32px; }
  .services-slide__title--heading:nth-child(1) {
    margin-left: 1.5%; }
  .services-slide__title--heading:nth-child(3) {
    margin-left: 51%; }
  .services-slide__title--heading:nth-child(5) {
    margin-left: 67%; } }

.prices__get-offer {
  border: none;
  background: transparent; }

.prices__get-offer:hover,
.prices__get-offer:focus {
  color: #ff3e79; }
