* {
  box-sizing: border-box; }

*::after,
*::-webkit-backdrop {
  box-sizing: border-box; }

*::after,
*::backdrop {
  box-sizing: border-box; }

ul {
  list-style: none;
  padding: 0;
  margin: 0; }

a {
  text-decoration: none; }

body {
  background: #f8f8f8;
  font-family: "TT Lakes", sans-serif;
  color: #1b1c21;
  font-size: 14px;
  line-height: 18px; }
